/**
 * Home
 */

.home .site-main {
  @include container;

  display: flex;
  flex-wrap: wrap;

  @include media( $third-break ) {
    justify-content: space-between;
    align-items: stretch;
    align-content: stretch;

    margin-top: 0;
  }
}

.home-posts {
  flex: 1 1 100%;

  @include media( $third-break ) {
    flex: 0 1 flex-grid(4);

    //margin-left: flex-gutter();
    //margin-right: flex-gutter();
    margin-bottom: 2em;

    background-color: color(white);
  }
}

.features-posts,
.events-posts,
.video-column-wrapper {
  @include media( $third-break ) {
    flex: 1 1 100%;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: stretch;
    align-content: stretch;

    //height: 625px;
    width: 100%;
    margin-left: 0;
    margin-right: 0;

    background-color: transparent;
  }

  .excerpted-post {
    box-shadow: $box-shadow;

    @include media( $third-break ) {
      border: 0;
    }
  }
}

.features-posts {
  @include media( $third-break ) {
    height: 720px;
  }

  > .section-header {
  }

  > .excerpted-post {
    &:after {
      display: none;
    }

    @include media( $third-break ) {
      flex: 1 1 flex-grid(8);

      margin-right: flex-gutter();
      margin-bottom: 0;
    }

    .entry-header {
      @include pad( 0 flex-gutter( 8 ) );
    }

    .entry-hero {
      margin-left: 0;

      @include media( $third-break ) {
        position: relative;
        height: 550px;
        max-height: none;
        margin-right: 0;
        overflow: hidden;
      }

      > img {
        @include media( $third-break ) {
          position: absolute;
          top: -9999px;
          right: -9999px;
          bottom: -9999px;
          left: -9999px;

          height: auto;
          max-height: none;
          min-height: 100%;
          width: auto;
          max-width: none;
          min-width: 100%;
          margin: auto;
        }
      }
    }
  }

  > .right-column {
    flex: 1 1 flex-grid(4);

    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: stretch;
    align-content: stretch;

    .excerpted-post {
      @include media( $third-break ) {
        flex: 1 1 auto;
        //flex: 1 1 auto; // needs ad

        max-height: 443px;
      }

      .entry-hero {
        margin-left: 0;

        @include media( $third-break ) {
          min-height: auto;
        }
      }
    }

    .ad {
      flex: 0 1 250px;

      display: flex;
      justify-content: center;
      align-items: center;
      align-content: center;

      min-width: 300px;
      min-height: 250px;

      > * {
        flex: 0 0 auto;
      }

      @include media( $third-break ) {
      }
    }
  }
}

.news-posts,
.arts-posts,
.culture-posts,
.gallery-posts,
.video-posts {
  margin-top: 2em;
  margin-bottom: 2em;

  background-color: color(white);

  box-shadow: $box-shadow;

  @include media( $third-break ) {
    //margin-bottom: 0;
  }

  .section-header {
    position: relative;
    z-index: 1;

    height: 0;

    .section-title {
      transform: translateY(-1em);
    }
  }

  .excerpted-post {
    &:first-of-type {
      .entry-hero {
        margin-left: 0;
      }

      .entry-header {
        margin-bottom: 0;
      }
    }

    &:nth-of-type(n + 2) {
      margin-left: flex-gutter(4);
      margin-right: flex-gutter(4);
    }
  }
}

.events-posts {
  @include media( $third-break ) {
    min-height: 400px;
  }

  .excerpted-post {
    .entry-hero {
      margin-left: 0;
    }

    @include media( $third-break ) {
      flex: 0 1 flex-grid(4);

      margin-bottom: 0;
    }

    &:nth-of-type(2) {
      padding-top: 0;
    }
  }
}

.home-posts + .ad,
.events-posts + .ad {
  flex: 1 0 100%;

  margin-bottom: 2em;
}

.gallery-posts {
  margin-top: 0;

  @include media( $third-break ) {
    margin-bottom: 2em;
  }

  > .excerpted-post {
    .entry-subhead {
      @include visuallyhidden;
    }
  }
}

.video-column-wrapper {
  flex-direction: column;

  flex-basis: 100%;

  @include media( $third-break ) {
    flex: 0 1 flex-grid(8);

    width: flex-grid(8);
    margin-bottom: 2em;
  }

  .video-posts {
    background-color: color(white);

    box-shadow: $box-shadow;

    @include media( $third-break ) {
      margin-top: 0;
    }

    .section-footer {
      padding-top: 0em;
      padding-bottom: 1em;
    }

    > .excerpted-post {
      flex-direction: column;

      margin-right: 0;
      padding-bottom: 0;

      border-bottom: 0;

      box-shadow: none;

      .entry-header {
        flex: 0 1 auto;
        order: 2;

        margin-left: flex-gutter(6);
        margin-right: flex-gutter(6);
        padding-right: 0;
        padding-bottom: 2em;
        padding-left: 0;

        border-bottom: 1px solid color(red);
      }

      .entry-hero {
        flex: 1 1 auto;
        order: 1;
        margin-bottom: 1em;
      }
    }
  }
}

.promotions-posts {
  margin-bottom: 2em;
  padding-right: flex-gutter(6);
  padding-bottom: 2em;
  padding-left: flex-gutter(6);

  background-color: color(white);

  box-shadow: $box-shadow;

  @include media( $third-break ) {
    flex: 0 1 flex-grid(8);

    margin-bottom: 2em;
    padding-right: flex-gutter();
    padding-left: flex-gutter();
  }

  .section-header {
    position: relative;
    z-index: 1;

    .section-title {
      transform: translateY(-1em);
    }
  }
}

.promotions-ad-column {
  flex: 1 1 auto;

  @include media( $third-break ) {
    flex: 0 1 flex-grid(4);
  }
}
